/* eslint-disable jsx-a11y/label-has-associated-control */
import React, { Fragment, useCallback, useEffect, useMemo, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useIntl } from 'gatsby-plugin-react-intl';
import csvtojson from 'csvtojson';
import ReactTable from 'react-table-v6';
import 'react-table-v6/react-table.css';
import { Button } from '@material-ui/core';

// Components
import { useFormik } from 'formik';
import SEO from '../components/SEO';
import Layout from '../components/Layout';
import { getCurrencyFormate, getInflationForYear, isClient, isObjectNotEmpty } from '../utils/utils';
import FormikInput from '../components/FormikInput';

const useStyles = makeStyles((theme) => ({
    buttonsWrapper: {
        textAlign: 'right',
        padding: '20px',
    },
    input: {
        display: 'block',
    },
    inputWrapper: {
        width: '50%',
        display: 'inline-block',
        paddingRight: '30px',
    },
    reactTable: {
        marginTop: '30px',
    },
}));

const EvaluationPage = () => {
    const intl = useIntl();
    const classes = useStyles();
    const [formData, setFormData] = useState({});
    // const [baseFormData, setBaseFormData] = useState({});
    const [dataYears, setDataYears] = useState([]);
    const personalInfoFields = ['name', 'email', 'phone'];

    const formik = useFormik({
        // initialValues: testValues || {},
        // enableReinitialize: true,
        // initialValues: baseFormData,
        initialValues: {},
        onSubmit: (values) => {
            // TODO
        },
        validate: (values, props) => {
            // TODO
        },
    });

    const handleAddYear = useCallback(() => {
        setDataYears([
            ...dataYears,
            (parseInt(dataYears[0], 10) + 1).toString(),
        ].sort((a, b) => b - a));
    }, [dataYears]);

    useEffect(() => {
        const getInflationData = async () => {
            const currentDate = new Date();
            const currentYear = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(currentDate);

            // eslint-disable-next-line no-restricted-syntax
            for (const year of dataYears) {
                let inflation = 0;
                if (year === currentYear) {
                    const currentDay = new Intl.DateTimeFormat('en', { day: '2-digit' }).format(currentDate);
                    const currentMonth = new Intl.DateTimeFormat('en', { month: 'numeric' }).format(currentDate);

                    const startDate = new Date(currentDate.setMonth(currentDate.getMonth() - 12));
                    const startDay = new Intl.DateTimeFormat('en', { day: '2-digit' }).format(startDate);
                    const startMonth = new Intl.DateTimeFormat('en', { month: 'numeric' }).format(startDate);
                    const startYear = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(startDate);

                    // eslint-disable-next-line no-await-in-loop
                    inflation = await getInflationForYear(
                        `${startDay}/${startMonth}/${startYear}`,
                        `${currentDay}/${currentMonth}/${currentYear}`
                    );
                } else {
                    // eslint-disable-next-line no-await-in-loop
                    inflation = await getInflationForYear(
                        `01/01/${year}`,
                        `31/12/${year}`
                    );
                }

                // eslint-disable-next-line no-await-in-loop
                await formik.setFieldValue(`${year}.inflation`, inflation);
            }
        };

        if (isClient()) {
            getInflationData();
        }
    }, [dataYears.length]);

    useEffect(() => {
        const getDataFromSpreadsheet = async () => {
            const urlParams = new URLSearchParams(window.location.search);
            const userId = urlParams.get('userId') || 0;
            const spreadsheetId = urlParams.get('spreadsheetId');

            if (!spreadsheetId) {
                return;
            }

            const response = await fetch(
                `https://docs.google.com/spreadsheets/d/e/${spreadsheetId}/pub?gid=1016831492&output=csv`, {
                    method: 'GET',
                }
            );

            const text = await response.text();
            const json = await csvtojson({ output: 'csv' })
                .fromString(text)
                .then((csvRow) => csvRow);

            const formValues = JSON.parse(json[userId][1]);

            // eslint-disable-next-line no-restricted-syntax
            for (const [year, value] of Object.entries(formValues)) {
                if (personalInfoFields.includes(year)) {
                    // eslint-disable-next-line no-continue
                    continue;
                }

                // eslint-disable-next-line no-await-in-loop
                await formik.setFieldValue(`${year}.numberOfRooms`, value.numberOfRooms);
                // eslint-disable-next-line no-await-in-loop
                await formik.setFieldValue(`${year}.occupancyRateIncreaseYoY`, value.occupancyRateIncreaseYoY);
                // eslint-disable-next-line no-await-in-loop
                await formik.setFieldValue(`${year}.pricePerRoomIncreaseYoY`, value.pricePerRoomIncreaseYoY);
                // eslint-disable-next-line no-await-in-loop
                await formik.setFieldValue(`${year}.averageSalesCommissionRate`, value.averageSalesCommissionRate);
            }

            setDataYears(
                Object.keys(formValues).filter(
                    (name) => !personalInfoFields.includes(name)
                ).sort((a, b) => b - a)
            );
            setFormData(formValues);
            // setBaseFormData(baseData);
        };

        if (isClient()) {
            getDataFromSpreadsheet();
        }
    }, []);

    const notExpenses = ['years', 'variables', 'revenue', 'basics'];
    // const revenueFields = [
    //     'numberOfRooms',
    //     'annualAverageOccupancyRate',
    //     'annualAveragePricePerRoom',
    // ];
    const expensesFields = [
        'otherExpenses',
        'waterAndSewage',
        'gas',
        'electricity',
        'landlinePhone',
        'cellphone',
        'internet',
        'cableTv',
        'insurance',
        'security',
        'accountingServices',
        'lawServices',
        'handlingAgentServices',
        'iptu',
        'otherFees',
        'marketingServices',
        'softwareServices',
        'onlineSalesCommission',
        'otherSalesCommission',
        'laundry',
        'bakery',
        'supermarket',
        'wages',
        'generalMaintenance',
    ];

    const formEntries = Object.entries(formData);
    const summedExpenses = formEntries.reduce((years, [currYear, data], idx) => ({
        ...years,
        [currYear]: (Object.entries(data)
            .filter(([attr, d]) => expensesFields.includes(attr))
            .reduce((acc, [attr, d]) => acc + parseFloat(d), 0)).toFixed(2),
    }), {});

    const occupancyRateIncreaseYoY = formEntries.reduce((years, [currYear, data], idx) => ({
        ...years,
        [currYear]: parseFloat(data.occupancyRateIncreaseYoY).toFixed(2),
    }), {});

    const averageSalesCommissionRate = formEntries.reduce((years, [currYear, data], idx) => ({
        ...years,
        [currYear]: parseFloat(data.averageSalesCommissionRate).toFixed(2),
    }), {});

    const pricePerRoomIncreaseYoY = formEntries.reduce((years, [currYear, data], idx) => ({
        ...years,
        [currYear]: parseFloat(data.pricePerRoomIncreaseYoY).toFixed(2),
    }), {});

    const numberOfRooms = formEntries.reduce((years, [currYear, data], idx) => ({
        ...years,
        [currYear]: parseInt(data.numberOfRooms, 10),
    }), {});

    const annualAverageOccupancyRate = formEntries.reduce((years, [currYear, data], idx) => ({
        ...years,
        [currYear]: parseFloat(data.annualAverageOccupancyRate).toFixed(2),
    }), {});

    const annualAveragePricePerRoom = formEntries.reduce((years, [currYear, data], idx) => ({
        ...years,
        [currYear]: parseFloat(data.annualAveragePricePerRoom).toFixed(2),
    }), {});

    // const summedRevenue = formEntries.reduce((years, [currYear, data], idx) => ({
    //     ...years,
    //     [currYear]: Object.entries(data)
    //         .filter(([attr, d]) => revenueFields.includes(attr))
    //         .reduce((acc, [attr, d]) => acc + parseFloat(d), 0),
    // }), {});

    const summedRevenue = formEntries.reduce((years, [currYear, data], idx) => ({
        ...years,
        [currYear]: ((parseFloat(data.annualAverageOccupancyRate) / 100)).toFixed(2)
            * parseFloat(data.annualAveragePricePerRoom),
    }), {});

    const { prefix: currencyPrefix } = getCurrencyFormate(intl.locale);

    const calculateValue = useCallback((name, year) => {
        // TODO move this to the useEffect
        switch (name) {
            case 'builder.occupation': {
                return annualAverageOccupancyRate[year];
            }

            case 'builder.average_price': {
                return annualAveragePricePerRoom[year];
            }

            case 'builder.gross_revenue': {
                return summedRevenue[year];
            }

            case 'builder.commissions': {
                return averageSalesCommissionRate[year];
            }

            case 'builder.net_revenue': {
                return summedRevenue[year]; // TODO minus taxes
            }

            case 'builder.operational_expenses': {
                return summedExpenses[year];
            }

            case 'builder.annual_net_income': {
                return summedRevenue[year] - summedExpenses[year];
            }

            case 'builder.years': {
                return year;
            }

            case 'builder.present_net_income': {
                return 2000;
            }

            case 'builder.name':
            default: {
                return year;
            }
        }
    }, [
        annualAverageOccupancyRate,
        annualAveragePricePerRoom,
        averageSalesCommissionRate,
        summedExpenses,
        summedRevenue,
    ]);

    const columns = useMemo(
        () => ['name', ...dataYears].map((name) => ({
            Header: name,
            accessor: name,
        })),
        [dataYears]
    );

    const tableData = useMemo(() => {
        const headers = [
            // 'name',
            'builder.occupation',
            'builder.average_price',
            'builder.gross_revenue',
            'builder.commissions',
            'builder.net_revenue',
            'builder.operational_expenses',
            'builder.annual_net_income',
            // 'builder.years',
            'builder.present_net_income',
        ];

        return headers.map((name) => [...dataYears].reduce((acc, year) => ({
            ...acc,
            name,
            [year]: calculateValue(name, year),
        }), {}));
    }, [dataYears, calculateValue]);

    const renderInput = useCallback((year, name, type, value = null) => (
        <FormikInput
            rows={1}
            className={classes.input}
            onChange={formik.handleChange}
            name={`${year}.${name}`}
            value={formik.values?.[year]?.[name] || value}
            error={formik.errors?.[year]?.[name]}
            type={type}
        />
    ), [classes.input, formik.errors, formik.handleChange, formik.values]);

    // useEffect(() => {
    //     // TODO
    // }, []);

    return (
        <Layout>
            <SEO
                title={intl.formatMessage({ id: 'home' })}
                robots="noindex, nofollow"
            />
            {isObjectNotEmpty(formData) && (
                <Fragment>
                    <h2>Details</h2>
                    <p>Name: {formData.name}</p>
                    <p>Email: {formData.email}</p>
                    <p>Phone: {formData.phone}</p>
                </Fragment>
            )}
            {isObjectNotEmpty(formData) && (
                <Button
                    onClick={handleAddYear}
                    variant="contained"
                    color="primary"
                >
                    Add year
                </Button>
            )}
            <section>
                {dataYears.map((year) => (
                    <div
                        key={year}
                        className={classes.inputWrapper}
                    >
                        <h2>{year}</h2>
                        {renderInput(year, 'inflation', 'percentage')}
                        {renderInput(year, 'pricePerRoomIncreaseYoY', 'percentage')}
                        {renderInput(year, 'occupancyRateIncreaseYoY', 'percentage')}
                        {renderInput(year, 'selic', 'percentage', '3.5')}
                        {renderInput(year, 'liquidityPremium', 'percentage', '13')}
                        {renderInput(year, 'discountRate', 'percentage', '16.50')}
                        {renderInput(year, 'numberOfRooms', 'number')}
                        {renderInput(year, 'nationalSimpleTax', 'percentage', '0')}
                        {renderInput(year, 'meiTax', 'percentage', '0')}
                        {renderInput(year, 'averageSalesCommissionRate', 'percentage')}
                    </div>
                ))}
            </section>
            {isObjectNotEmpty(formData) && (
                <ReactTable
                    className={classes.reactTable}
                    columns={columns}
                    data={tableData}
                />
            )}
        </Layout>
    );
};

export default EvaluationPage;
